.image-container {
    width: 100vw;
    height: 100vh;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inner-container {
    background-color:tomato;
    width: 40%;
    padding: 20px;
    border-radius: 3px;
    color: #333;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
    ;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    font-size: 1.2em;
}

cite {
    margin-bottom: 15px;
}

button {
    padding: 12px;
    background-color: #333;
    color: tomato;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}